<template>
  <transition name="slide">
  <div class="popup-menu" v-if="currentMenu.opened">
    <div :class="[ 'header', currentMenu.style ]">
      <h1 v-html="currentMenu.title"></h1>
      <h3>
        <span v-html="currentMenu.subtitle"></span>
        <span class="right">{{ currentMenu.index + 1 }} / {{ currentMenu.items.length }}</span>
      </h3>
      <div class="clear"></div>
    </div>

    <ul>
      <li v-for="(item, index) in currentMenu.items" v-if="item.visible"
          :class="[item.style, { active: currentMenu.index == index }]"
          v-on:click="processClick(currentItem, index)">
        <div v-if="Object.prototype.toString.call(item.value) !== '[object Array]'">
          <span v-if="item.value" v-html="item.key"></span>
          <span v-if="item.value" v-html="item.value"></span>
          <div v-else>{{ item.key }}</div>
        </div>
        <div v-else>
          <span>{{ item.key }}</span>
        <span>
          <selection :list="item.value" :index="item.index"></selection>
        </span>
        </div>
      </li>
    </ul>

    <div class="scroll-line" v-if="currentMenu.items.length > currentMenu.limit"></div>
    <div class="help" v-if="help"><p>{{ help }}</p></div>
    <div v-if="currentMenu.colorPicker">
      <color-selector :name="currentMenu.colorPicker.name"
                      :colors="currentMenu.colorPicker.colors"
                      :index="currentMenu.colorPicker.index"></color-selector>
    </div>
    <div v-if="currentMenu.grid">
      <xygrid :x="currentMenu.grid.x"
              :y="currentMenu.grid.y"
              :top="currentMenu.grid.top"
              :bottom="currentMenu.grid.bottom"
              :left="currentMenu.grid.left"
              :right="currentMenu.grid.right"></xygrid>
    </div>

    <div class="stats" v-if="currentMenu.Stats">
      <div v-for="item in currentMenu.Stats">
        <span>{{ item.name }}</span>
        <stage :levels="item.levels"
               :value="item.value"
               :width="item.width">
        </stage>
      </div>
    </div>
  </div>
  </transition>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'
  import Selection from './Selection'
  import ColorSelector from './ColorSelector'
  import Grid from './Grid'
  import Stage from './Stage'

  export default {
    components: {
      Selection,
      ColorSelector,
      Grid,
      Stage
    },
    props: {

    },
    computed: {
      ...mapGetters({
        currentMenu: 'getCurrentMenu',
        currentItem: 'currentItem',
        menu: 'menu',
        menuStack: 'menuStack',
        help: 'help'
      })
    },
    methods: {
      ...mapActions({
        processClick: 'processClick',
        returnBack: 'returnBack',
        prevIndex: 'prevIndex',
        nextIndex: 'nextIndex',
        prevSelectionItem: 'prevSelectionItem',
        nextSelectionItem: 'nextSelectionItem',
        prevColorItem: 'prevColorItem',
        nextColorItem: 'nextColorItem'
      })
    },
    mounted: function(){
      var self = this;
      var limit = this.currentMenu.limit > this.currentMenu.items.length ? this.currentMenu.items.length : this.currentMenu.limit;

      if (this.currentMenu.items.length > limit) {
        for (var i = limit; i < this.currentMenu.items.length; i++)
          this.currentMenu.items[i].visible = false;
      }

      window.addEventListener('wheel', function(e) {
        var delta = e.deltaY || e.detail || e.wheelData;

        if (delta > 0) {
          self.currentMenu.index++;
        } else if(delta < 0) {
          self.currentMenu.index--;
        }
      });

      this.$nextTick(function() { // WARNING: Hardcoded

        if (this.$vnode.elm.getElementsByClassName('xygrid').length === 0)
          return;

        var grid = this.$vnode.elm.getElementsByClassName('xygrid')[0].children[1];
        var clicked = false;

        grid.addEventListener('mousedown', function(e) { // Testing. Remove in production
          if (e.which === 1) { // LMB
            var pos = getElementPosition(e.currentTarget);

            var x = e.clientX - pos.x;
            var y = e.clientY - pos.y;

            if (self.currentMenu.grid && (x < 136 && y < 136)) {
              self.currentMenu.setGridXY(x / 130, y / 130);
            }

            clicked = true;
          }
        });

        window.addEventListener('mouseup', function(e) {
          if (e.which === 1) { // LMB
            clicked = false;
          }
        });

        grid.addEventListener('mousemove', function(e) {
          if (clicked) {
            var pos = getElementPosition(e.currentTarget);

            var x = e.clientX - pos.x;
            var y = e.clientY - pos.y;

            if (self.currentMenu.grid && (x < 136 && y < 136)) {
              self.currentMenu.setGridXY(x / 130, y / 130);
            }
          }
        });
      });

      window.addEventListener('keydown', function(e) {
        console.log(e.keyCode);
        if (e.keyCode == 38) { // up
          self.prevIndex()
        } else if (e.keyCode == 40) { // down
          self.nextIndex()
        } else if (e.keyCode == 13) { // enter
          self.processClick(self.currentMenu.index);
        } else if (e.keyCode == 8) { // backspace
          self.returnBack();
        }

        if (self.currentItem != null) {
          if (e.keyCode == 37) { // left
            self.prevSelectionItem();
          } else if (e.keyCode == 39) { // right
            self.nextSelectionItem();
          }
        }

        if (self.currentMenu.colorPicker) // Testing. Remove in production
        {
          if (e.keyCode == 37) { // left
            self.prevColorItem();
          } else if (e.keyCode == 39) { // right
            self.nextColorItem();
          }
        }

        return false;
      });
    },
    watch: {
      'currentMenu.index': function (val, old) { // Change selected item depending on current index

        if (val > (this.currentMenu.items.length - 1)) {

          for (var i = 0; i < this.currentMenu.items.length; i++) { // Maybe it possible without loop and additional bindngs
            if (i >= this.currentMenu.limit) {
              this.currentMenu.items[i].visible = false;
            } else {
              this.currentMenu.items[i].visible = true;
            }
          }

          this.currentMenu.index = 0;

          return;

        } else if (val < 0) {

          for (var i = 0; i < this.currentMenu.items.length; i++) {
            if (this.currentMenu.items.length - i > this.currentMenu.limit) {
              this.currentMenu.items[i].visible = false;
            } else {
              this.currentMenu.items[i].visible = true;
            }
          }

          this.currentMenu.index = this.currentMenu.items.length - 1;

          return;
        }

        if ((typeof this.currentMenu.onItemChange) === 'function') {
          this.currentMenu.onItemChange(this.currentMenu.index);
        }

        if (val > (this.currentMenu.limit - 1)) {
          this.currentMenu.items[val - this.currentMenu.limit].visible = false;
        } else if ((this.currentMenu.items.length - val) > this.currentMenu.limit) {
          this.currentMenu.items[val + this.currentMenu.limit].visible = false;
        }

        this.currentMenu.items[val].visible = true;

        if (this.currentMenu.items[val].help) {
          this.help = this.currentMenu.items[val].help;
        } else {
          this.help = null;
        }
      }
    }
  }
</script>

<style lang="scss">
  .slide-enter-active, .slide-leave-active {
    left:0;
    transition: all .5s;
  }
  .slide-enter, .slide-leave-to{
    left:-100%;
  }
</style>



