<template>
  <div class="selection">
    <li v-for="(item, ind) in list" :class="{ active: index === ind }"><span :style="item.s1">{{ item.n1 }}</span> <span :style="item.s2">{{item.n2}}</span> <span :style="item.s3">{{item.n3}}</span></li>
  </div>
</template>

<script>

  export default {
    name: "selection-item",
    props: {

      index: {
        type: Number,
        default: 0
      },
      list: Array
    }
  }
</script>

<style lang="scss">

</style>



