import Vue from 'vue'
import Vuex from 'vuex'
import initialStates from './initial-currentmenu-state'

Vue.use(Vuex)

const state = {
  menuStack: new Array(),
  help: null,
  currentMenu: initialStates.currentMenu()
};

const mutations = {
  TOGGLE_MENU: (state, toggle) => {
    state.currentMenu.opened = toggle;
  },
  SET_MENU: (state, menu) => {
    state.currentMenu = initialStates.currentMenu();
    state.currentMenu.title = menu.title;
    state.currentMenu.subtitle =  menu.subtitle;
    state.currentMenu.limit = menu.limit;
    state.currentMenu.slider = menu.slider;
    state.currentMenu.grid = menu.grid;
    state.currentMenu.style = menu.style;
    state.currentMenu.onItemChange = menu.onItemChange;
    state.currentMenu.index = menu.index;
    menu.items.forEach(function(e, index){
      if (index > menu.limit -1) {
        e.visible = false
      } else {
        e.visible = true
      }
      store.commit('ADD_ITEM', e);
    });
    if ( menu.stats !== null ) {
      menu.stats.forEach(function(e){
        store.commit('ADD_STATS', e);
      })
    } else {
      state.currentMenu.stats = null;
    }
    if ( menu.colorPicker !== null){
      state.currentMenu.colorPicker = {
        name: menu.colorPicker.name,
        colors: menu.colorPicker.colors,
        index: menu.colorPicker.index || 0,
        onChange: menu.colorPicker.onChange
      };
    }
    state.menuStack.push(state.currentMenu);
  },
  ADD_ITEM: (state, item) => {
    state.currentMenu.items.push({
      visible: item.visible,
      index:  item.index || 0,
      style:  item.style || null,
      value:  item.value || null,
      key:  item.key || null,
      help:  item.help || null,
      submenu: item.submenu || null,
      action:  item.action || null,
      onChange: item.onChange || null,
      back: item.back  || null

  })
  },
  ADD_STATS: (state, stat) => {
    state.currentMenu.Stats.push(stat);
  },
  RESET_STATS: (state, stat) => {
    state.currentMenu.Stats = [];
  },
  RETURN_BACK: (state) => {
    if (state.menuStack.length > 1) {
      state.menuStack.pop();
      state.currentMenu = state.menuStack[ state.menuStack.length - 1];
    } else {
      state.currentMenu.opened = false;
      $.post('http://newmenu/close', JSON.stringify({}))
    }
  },
  NEXT_MENU_INDEX: (state) => {
    state.currentMenu.index = state.currentMenu.index+1;
  },
  PREV_MENU_INDEX: (state) => {
    state.currentMenu.index = state.currentMenu.index-1;
  },
  PREV_ITEM_INDEX: (state) => {
    state.currentMenu.items[state.currentMenu.index].index = state.currentMenu.items[state.currentMenu.index].index-1;
    if (state.currentMenu.items[state.currentMenu.index].index < 0) {
      state.currentMenu.items[state.currentMenu.index].index = state.currentMenu.items[state.currentMenu.index].value.length - 1;
    }
  },
  NEXT_ITEM_INDEX: (state) => {
    state.currentMenu.items[state.currentMenu.index].index = state.currentMenu.items[state.currentMenu.index].index+1;
    if (state.currentMenu.items[state.currentMenu.index].index > state.currentMenu.items[state.currentMenu.index].value.length - 1) {
      state.currentMenu.items[state.currentMenu.index].index = 0;
    }
  },
  PREV_COLORPICKER_INDEX: (state) => {
    state.currentMenu.colorPicker.index = state.currentMenu.colorPicker.index-1;
    if (state.currentMenu.colorPicker.index < 0) {
      state.currentMenu.colorPicker.index = state.currentMenu.colorPicker.colors.length - 1;
    }
  },
  NEXT_COLORPICKER_INDEX: (state) => {
    state.currentMenu.colorPicker.index = state.currentMenu.colorPicker.index+1;
    if (state.currentMenu.colorPicker.index > state.currentMenu.colorPicker.colors.length - 1) {
      state.currentMenu.colorPicker.index = 0;
    }
  }
};

const getters = {
  getCurrentMenu: (state) => state.currentMenu,
  currentItem: (state) => state.currentMenu.items[state.currentMenu.index],
  menu: (state) => state.currentMenu,
  menuStack: (state) => state.menuStack,
  help: (state) => state.help,
  getStats: (state) => state.currentMenu.Stats
};

const actions = {
  processClick(store) {
    if (store.state.currentMenu.items[store.state.currentMenu.index].back) {
      if (store.state.currentMenu.items[store.state.currentMenu.index].back === true) {
        store.commit('RETURN_BACK', {})
      }
    } else {
      if (store.state.currentMenu.items[store.state.currentMenu.index].action !== null
        && (typeof store.state.currentMenu.items[store.state.currentMenu.index].action) == 'function') {
        store.state.currentMenu.items[store.state.currentMenu.index].action();
      }
    }
  },
  returnBack(store) {
    store.commit('RETURN_BACK', {})
  },
  prevIndex(store) {
    store.commit('PREV_MENU_INDEX', {})
  },
  nextIndex(store) {
    store.commit('NEXT_MENU_INDEX', {})
  },
  prevSelectionItem(store) {
    store.commit('PREV_ITEM_INDEX', {})
    state.currentMenu.items[state.currentMenu.index].onChange(state.currentMenu.items[state.currentMenu.index].index)
  },
  nextSelectionItem(store) {
    store.commit('NEXT_ITEM_INDEX', {})
    state.currentMenu.items[state.currentMenu.index].onChange(state.currentMenu.items[state.currentMenu.index].index)
  },
  prevColorItem(store) {
    store.commit('PREV_COLORPICKER_INDEX', {})
    store.state.currentMenu.colorPicker.onChange(state.currentMenu.colorPicker.colors[state.currentMenu.colorPicker.index])
  },
  nextColorItem(store) {
    store.commit('NEXT_COLORPICKER_INDEX', {});
    store.state.currentMenu.colorPicker.onChange(state.currentMenu.colorPicker.colors[state.currentMenu.colorPicker.index])
  }
};

let store = new Vuex.Store({
  state: state,
  mutations: mutations,
  getters: getters,
  actions: actions
});
global.store = store;

export default store



